import React, {useState} from 'react';
import {  makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import SwipeableDrawer from '@material-ui/core/SwipeableDrawer';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import FacebookIcon from '@material-ui/icons/Facebook';
import TwitterIcon from '@material-ui/icons/Twitter';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import Divider from '@material-ui/core/Divider'
import HomeIcon from '@material-ui/icons/Home';
import AirportShuttleIcon from '@material-ui/icons/AirportShuttle'
import WorkIcon from '@material-ui/icons/Work';
import InfoIcon from '@material-ui/icons/Info'
import LocalMallIcon from '@material-ui/icons/LocalMall';
import GroupIcon from '@material-ui/icons/Group';
import ContactSupportIcon from '@material-ui/icons/ContactSupport';
import AccessibilityNewIcon from '@material-ui/icons/AccessibilityNew';
import BrightnessAutoIcon from '@material-ui/icons/BrightnessAuto';
import ListItemText from '@material-ui/core/ListItemText';
import {Link} from "react-router-dom";


const useStyles = makeStyles(theme => ({
    root: {
      flexGrow: 1,
    },
    appBar: {
      background: `#141e30`,
      background: `-webkit-linear-gradient(to right, #141e30, #243b55)`,
      background: `linear-gradient(to right, #141e30, #243b55)`,
      postion: 'relative'
    },
    drawer: {
        background: `#141e30`,
        postion: 'relative',
        color: 'white',
    },
    menuButton: {
      marginRight: theme.spacing(2),
      
    },
    title: {
      flexGrow: 1,
      fontFamily: 'GilroyLight, sans-serif'
    },
    button: {
        fontFamily: 'GilroyLight, sans-serif',
        marginLeft: theme.spacing(2),
    },
    link: {
        textDecoration: 'none',
        color: 'white',
        fontFamily: 'K2D',
    },
    icon: {
        color: 'white'
    },
    box: {
        float: 'right'
    },
    divider: {
      borderTop: '1px solid white'
    }
  }));

function AveryBar () {


    const sideList = side => (
        <div
          className={classes.list}
          role="presentation"
          onClick={toggleDrawer}
        >
          <List>
              <Link to="/" className={classes.link}>
                <ListItem button>
                  <ListItemIcon> <HomeIcon className={classes.icon} /></ListItemIcon>
                  <ListItemText primary="Home" />
                </ListItem>
              </Link>
              <Link to="/about" className={classes.link}>
                <ListItem button>
                  <ListItemIcon> <InfoIcon className={classes.icon} /> </ListItemIcon>
                  <ListItemText primary="About" />
                </ListItem>
              </Link>
              <Divider className={classes.divider}/>
              <Link to="/services" className={classes.link}>
                <ListItem button>
                  <ListItemIcon> <WorkIcon className={classes.icon} /> </ListItemIcon>
                  <ListItemText primary="Services" />
                </ListItem>
              </Link>
              <Link to="/contact" className={classes.link}>
                <ListItem button>
                  <ListItemIcon> <ContactSupportIcon className={classes.icon} /></ListItemIcon>
                  <ListItemText primary="Contact Us" />
                </ListItem>
              </Link>
          </List>
          <Divider className={classes.divider} />
            <a href="#" className={classes.link} target="_blank" rel="noopener noreferrer">
            <ListItem button>
                <ListItemIcon> <FacebookIcon className={classes.icon} /></ListItemIcon>
                <ListItemText primary="Facebook Page" />
              </ListItem>
            </a>
        </div>
    );
    const classes = useStyles();
    const [drawer, setDrawer] = useState(false)

    function toggleDrawer() {
        setDrawer(!drawer)
    }

    return (
        <div className={classes.root}>
            <AppBar position="static"  className={classes.appBar}>
                <Toolbar>
                <IconButton onClick={toggleDrawer}  edge="start" className={classes.menuButton} color="inherit" aria-label="menu">
                    <MenuIcon />
                </IconButton>
                <Typography variant="h6" className={classes.title}>
                    JSM Services
                </Typography>
                </Toolbar>
            </AppBar>
            <SwipeableDrawer  classes={{paper: classes.drawer}} disableDiscovery="true" disableSwipeToOpen="true" open={drawer} onClose={toggleDrawer} anchor="left">
                {sideList('left')}
            </SwipeableDrawer>
    </div>
    )
}

export default AveryBar;